.container {
  display: grid;
  grid-template-columns: 1fr 1032px 1fr;
  grid-template-rows: 1fr;
  gap: 0 0;
  grid-auto-flow: row;
  grid-template-areas: ". . .";

  margin: 48px 0 0 0;
}

.content_container {
  display: grid;
  grid-template-columns: 324px 708px;
  grid-template-rows: 1fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas: ". .";

  background-color: #ffffff;
  border-radius: 30px;
}

.back {
  margin-right: 37px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
}

.button {
  all: unset;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: center;

  &:hover {
    opacity: 0.6;
    transition: 0.3s;
  }

  &:active {
    opacity: 1;
  }
}

.btnIcon {
  background-image: url(../../images/btn-back.svg);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  width: 24px;
  height: 24px;
  margin-right: 4px;
}

.sidebar {
  margin: 20px;
}

.request_creator {
  overflow: auto;
  height: 75vh;
}
